const GET_EVENTS = `
  query GetEvents {
    allEvents {
      id
      name
      eventName
    }
  }
`;

function graphql(query, variables = {}) {
  return fetch('http://localhost:3000/admin/api', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      variables,
      query,
    }),
  }).then(function(result) {
    return result.json();
  });
}

import BackButton from "@/components/BackButton";

export default {
  head: {
    title: 'Events',

    meta: [
      {
        name: "viewport",
        content: 'width=device-width, initial-scale=1' 
      }
    ]
  },  

  data() {
    return {
      newEvent: '',
    };
  },

  // Get the event on server side
  async asyncData(context) {
    const { data } = await graphql(GET_EVENTS);
    return {
      event: data.allEvents.find(el => el.id === context.params.id )
    };
  },

  methods: {
    async getevents() {
      const { data } = await graphql(GET_EVENTS);
      this.event = data.allEvents.find(el => el.id === context.params.id );
    },
    handleSubmit() {
      setTimeout("alert('Thank you for RSVP.')", 1000);
      this.$router.back();
    }
  },
  components: {
    BackButton
  }
};

